.item1 {
  text-align: justify;
  flex-direction: column;
  margin-top : 550px;
}

.create-btn {
  color: #fff;
  background-color: #712cf9;
  margin-bottom: 5px;
  display: block;
  padding-left: 15px;
  padding-right: 15px;
}

.create-btn:hover {
  color: #fff;
  background-color: #8048f2;
}

.login-btn2 {
  color: #000000;
  background-color: #b38efc;
  display: block;
  padding-left: 15px;
  padding-right: 15px;
}


.login-btn2:hover {
  color: #fff;
  background-color: #712cf9;
}

/* ==MD== */

@media screen and (max-width:1024px) {
  .container {
      grid-template-columns: 1fr 1fr;
      gap: 1.2rem;
  }
}

/* ==SD== */

@media screen and (max-width:600px) {
  .container {
      grid-template-columns: 1fr;
      gap: 1rem;
  }
}
